import Vue from 'vue'
import Router from 'vue-router'
import Liu from '@/components/mirrors'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: Liu
    },
    {
      path: '/mirrors',
      'name': 'Liu',
      component: Liu
    }
  ]
})



