<template>
  <div class="hello">
    <el-table
      :data="tableData"
      border
      style="width:100%">
      <el-table-column
        label="标题"
        header-align="center">>
        <template slot-scope="scope">
          <div slot="reference" class="name-wrapper">
            <a :href="scope.row.url">{{ scope.row.name }}</a>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        prop="url"
        label="链接"
        header-align="center">>
      </el-table-column>
      <el-table-column
        prop="type"
        label="网站类型"
        header-align="center">>

      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'Liu',
  data () {
    return {
      tableData: []
    }
  },
  mounted () {
    this.get_data()
  },
  methods: {
    get_data: function () {
      var data = require('../files/mirrorurls.json')
      this.tableData = data.mirrors
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
</style>



